* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.nav-floater {
  z-index: 99;
  width: 30px;
  height: 50px;
  cursor: pointer;
  transform-origin: 100% 100%;
  background: #fff;
  justify-content: center;
  align-items: center;
  margin: auto 0;
  transition: all .35s cubic-bezier(.91, -.28, .06, 1.3), background-color .25s, color .25s;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
}

.next-floater {
  animation: .7s 2.4s forwards comeInRight;
  right: -100px;
}

.prev-floater {
  animation: .7s 2.4s forwards comeInLeft;
  left: -100px;
}

@keyframes comeInRight {
  0% {
    right: -100px;
  }

  100% {
    right: 0;
  }
}

@keyframes comeInLeft {
  0% {
    left: -100px;
  }

  100% {
    left: 0;
  }
}

@keyframes tilt-shaking {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(7px);
  }

  50% {
    transform: translateY(-7px);
  }

  75% {
    transform: translateY(7px);
  }

  100% {
    transform: translateY(0);
  }
}

.nav-floater:hover {
  color: #ccc;
  background-color: #4d4d4d;
}

.nav-floater span {
  margin-top: -3px;
  font-size: 1.4rem;
}

.swipe-load {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #a76ee7;
  display: flex;
  position: fixed;
  overflow: hidden;
}

.swipe-1 {
  z-index: 101;
  height: 100%;
  background: #ff8b5d;
  animation: .7s forwards slideInLeft;
  position: fixed;
}

.swipe-2 {
  z-index: 102;
  height: 100%;
  background: #567ce4;
  animation: 1.8s forwards slideInLeft;
  position: fixed;
}

.swipe-3 {
  z-index: 103;
  height: 100%;
  background: #a76ee7;
  animation: 2.4s forwards slideInLeft;
  position: fixed;
}

@keyframes slideInLeft {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

